import {
  Box,
  Button,
  Heading,
  HStack,
  Spacer,
  Drawer,
  DrawerBody,
  FormControl,
  FormLabel,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  useDisclosure,
  Input,
  Text,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalCloseButton,
  Center,
  Stack,
  Divider,
} from '@chakra-ui/react'
import React, { useState } from 'react'
import { FiArrowLeft, FiEdit, FiPlus, FiTrash } from 'react-icons/fi'
import useFetch from 'use-http'
import { UserStore } from '../store/user'

function Companies() {
  const companyDrawer = useDisclosure()
  const { user } = UserStore.useState()
  const api = useFetch('/api/add/request_company_details_change')
  const addApi = useFetch('/api/add/company')
  const [alert, setAlert] = useState({
    visible: false,
    message: '',
  })

  const [newCompany, setNewCompany] = useState({
    name: '',
    gst: '',
    entry: false,
  })

  const confirmAdd = () => {
    addApi
      .post({
        company_name: newCompany.name,
        gst_no: newCompany.gst,
      })
      .then((res) => {
        if (res.message === 'Success') {
          setNewCompany({
            name: '',
            gst: '',
            entry: false,
          })
        }
        companyDrawer.onClose()
        setTimeout(() => {
          window.location.reload()
        }, 800)
      })
  }

  const editRemove = (id: string, type: string) => {
    api
      .post({
        company_id: id,
        request_type: type,
      })
      .then((res) => {
        if (res.message === 'Success') {
          setAlert({
            visible: true,
            message: 'Request sent successfully',
          })
        } else {
          setAlert({
            visible: true,
            message: 'Request already pending',
          })
        }
      })
  }

  return (
    <Box px={6} py={6}>
      <HStack>
        <Box
          fontSize={{ base: '3xl', md: '4xl' }}
          onClick={() => {
            window.history.back()
          }}
          cursor={'pointer'}
        >
          <FiArrowLeft />
        </Box>
        <Text fontWeight={'bold'} fontSize={{ base: 'xl', md: '3xl' }}>
          Companies
        </Text>
        <Spacer />
        <Box>
          <Button
            fontWeight={'bold'}
            rounded={'full'}
            fontSize={{ base: 'xs', md: 'lg' }}
            leftIcon={<FiPlus />}
            colorScheme={'orange'}
            textColor={'white'}
            onClick={() => {
              companyDrawer.onOpen()
            }}
          >
            ADD COMPANY
          </Button>
        </Box>
      </HStack>
      <Box mt={8} d={{ base: 'none', lg: 'flex' }}>
        <Table size={'lg'} variant='simple'>
          <Thead>
            <Tr>
              <Th>Name</Th>
              <Th>GST</Th>
              <Th isNumeric></Th>
            </Tr>
          </Thead>
          <Tbody>
            {user?.companies.map((company: any, idx: number) => {
              return (
                <Tr key={idx} mt={8}>
                  <Td fontSize={'xl'}>{company.company_name}</Td>
                  <Td fontSize={'xl'}>{company.gst_no}</Td>

                  <Td cursor={'pointer'} isNumeric>
                    <HStack spacing={6}>
                      <Button
                        variant={'link'}
                        leftIcon={<FiEdit />}
                        colorScheme={'blue'}
                        onClick={() => {
                          editRemove(company._id, 'Edit')
                        }}
                      >
                        Edit
                      </Button>
                      <Button
                        variant={'link'}
                        colorScheme={'orange'}
                        leftIcon={<FiTrash />}
                        onClick={() => {
                          editRemove(company._id, 'Delete')
                        }}
                      >
                        Delete
                      </Button>
                    </HStack>
                  </Td>
                </Tr>
              )
            })}
          </Tbody>
        </Table>
      </Box>
      <Box mt={8} d={{ base: 'block', lg: 'none' }}>
        <Box>
          <Stack spacing={5}>
            {user?.companies.map((company: any, idx: number) => {
              return (
                <>
                  <HStack key={idx}>
                    <Box>
                      <Text fontSize={'xs'}>GST NO: {company.gst_no}</Text>
                      <Text fontWeight={'bold'} fontSize={'xs'}>
                        {company.company_name}
                      </Text>
                    </Box>
                    <Spacer />
                    <HStack spacing={2}>
                      <Button
                        variant={'link'}
                        onClick={() => {
                          editRemove(company._id, 'Edit')
                        }}
                        colorScheme={'blue'}
                        leftIcon={<FiEdit />}
                        size={'sm'}
                      >
                        Edit
                      </Button>
                      <Button
                        variant={'link'}
                        onClick={() => {
                          editRemove(company._id, 'Delete')
                        }}
                        size={'sm'}
                        colorScheme={'orange'}
                        leftIcon={<FiTrash />}
                      >
                        Delete
                      </Button>
                    </HStack>
                  </HStack>
                  <Divider />
                </>
              )
            })}
          </Stack>
        </Box>
      </Box>
      <Box>
        <Drawer
          isOpen={companyDrawer.isOpen}
          placement='right'
          onClose={companyDrawer.onClose}
          size={'md'}
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerHeader>
              <HStack spacing={6}>
                <Box
                  fontSize={{ base: '2xl', lg: '4xl' }}
                  onClick={companyDrawer.onClose}
                  cursor={'pointer'}
                >
                  <FiArrowLeft />
                </Box>
                <Heading size='md'>Add Company</Heading>
              </HStack>
            </DrawerHeader>

            <DrawerBody>
              <FormControl>
                <FormLabel>Enter company name</FormLabel>
                <Input
                  onChange={(e) => {
                    setNewCompany({ ...newCompany, name: e.target.value })
                  }}
                />
              </FormControl>
              <FormControl mt={6}>
                <FormLabel>Enter GST Number</FormLabel>
                <Input
                  onChange={(e) => {
                    setNewCompany({ ...newCompany, gst: e.target.value })
                  }}
                />
              </FormControl>
              <Box mt={12}>
                <Button
                  textColor={'white'}
                  fontWeight={'bold'}
                  rounded={'full'}
                  w={{ base: '50%', md: 'full' }}
                  colorScheme={'orange'}
                  onClick={confirmAdd}
                >
                  ADD
                </Button>
              </Box>
            </DrawerBody>
          </DrawerContent>
        </Drawer>
      </Box>
      <Box>
        <Modal
          isOpen={alert.visible}
          onClose={() => {
            setAlert({ ...alert, visible: false })
          }}
        >
          <ModalOverlay />
          <ModalContent>
            <ModalHeader>Alert</ModalHeader>
            <ModalCloseButton />
            <ModalBody>
              <Text>{alert.message}</Text>
              <Center>
                <Box mt={4}>
                  <Button
                    onClick={() => {
                      setAlert({ ...alert, visible: false })
                    }}
                    colorScheme={'red'}
                  >
                    Confirm
                  </Button>
                </Box>
              </Center>
            </ModalBody>
          </ModalContent>
        </Modal>
      </Box>
    </Box>
  )
}

export default Companies
