import useFetch from 'use-http'
import {
  Box,
  Button,
  ButtonGroup,
  Heading,
  HStack,
  Spacer,
  Stack,
  Text,
} from '@chakra-ui/react'
import useRazorpay from 'react-razorpay'
import { UserStore } from '../store/user'
import { useEffect, useState } from 'react'
import { FiArrowLeft, FiCheck } from 'react-icons/fi'
import { Link } from 'react-router-dom'

const BENIFITS = {
  '1': 'You will be able to access our BUY COAL feature to purchase coal online transparently.',
  '2': 'You will be able to bid your own price at which you want to buy coal.',
  '3': 'You will be able to access real time Market Price. You will also know what conditions are applicable on what market price.',
  '4': 'The Security Money is fully refundable subject to terms and conditions.',
}

const Upgrade = () => {
  const Razorpay = useRazorpay()
  const [message, setMessage] = useState('')
  const listingApi = useFetch('/api/listing')
  const subscriptionFeesApi = useFetch('/api/listing/subscription_fees')

  const paymentApi = useFetch('/api/add/createorder')
  const paymentVerifyApi = useFetch('/api/add/verifyAndStore')
  const userStore = UserStore.useState()
  const [lastPointMessage, setLastPointMessage] = useState('')
  const [amount, setAmount] = useState<any>('')

  const fetchAmount = () => {
    subscriptionFeesApi.post().then((res) => {
      setAmount(res.data)
    })
  }

  useEffect(() => {
    fetchAmount()
    listingApi.post('upgrade_english_message').then((res) => {
      setLastPointMessage(res.data)
    })
  }, [])

  const createPayment = () => {
    paymentApi
      .post({
        amountInPaise: amount * 100,
        type: 'upgrade',
      })

      .then((data) => {
        if (!data.orderId) return

        const { orderId } = data
        // console.log('data', data)
        const options = {
          key: 'rzp_live_W8oi4FR1FqKtxw', // Enter the Key ID generated from the Dashboard
          amount: amount.toString(),
          currency: 'INR',
          name: 'Anmol India LTD.',
          description: 'Anmol India LTD.',
          order_id: orderId,
          handler: async function (response: any) {
            console.log('response', response)
            paymentVerifyApi
              .post({
                ...response,
                user_id: userStore.data.user.id,
                amount,
                createOrderId: orderId,
                remarks: 'upgrade',
              })
              .then((data) => {
                setMessage(data.message)
              })
          },

          notes: {
            type: 'upgrade',
          },
        }

        const rzp1 = new Razorpay(options)

        rzp1.on('payment.failed', function (response: any) {
          console.log('response', response)
          setMessage(response.description)
        })

        rzp1.open()
      })
  }
  return (
    <Box>
      <Box maxW={'4xl'} m={'0 auto'} px={{ base: 8, lg: 0 }}>
        <HStack py={6}>
          <Box
            fontSize={{ base: '2xl', lg: '4xl' }}
            onClick={() => {
              window.history.back()
            }}
            cursor={'pointer'}
          >
            <FiArrowLeft />
          </Box>

          <Heading size={'lg'}>Upgrade</Heading>
        </HStack>
        <Text color={'gray.500'} fontSize={{ base: 'xl', lg: 'xl' }}>
          By depositing the security money, you can upgrade your membership for
          free.
        </Text>
        <Text
          fontSize={{ base: 'xl', lg: 'xl' }}
          mt={6}
          fontWeight='semibold'
          textColor={'gray.400'}
        >
          PRICING
        </Text>
        <Text mt={6} fontSize={'2xl'} fontWeight='semibold'>
          {Number(amount).toLocaleString()}
        </Text>

        <Text
          mb={2}
          fontSize={'md'}
          mt={6}
          fontWeight='semibold'
          textColor={'gray.400'}
        >
          BENIFITS
        </Text>
        <Stack spacing={3} mt={4}>
          {Object.keys(BENIFITS).map((key) => {
            const val = BENIFITS[key]
            return (
              <HStack alignItems={'flex-start'} spacing={4}>
                <Box fontSize={'xl'} color='green.400'>
                  <FiCheck />
                </Box>
                <Text fontSize={'lg'}>{val}</Text>
              </HStack>
            )
          })}
        </Stack>
        <Box mt={4}>
          <HStack alignItems={'flex-start'} spacing={4}>
            <Box fontSize={'xl'} color='green.400'>
              <FiCheck />
            </Box>
            <Text fontSize={'lg'}>{lastPointMessage}</Text>
          </HStack>
        </Box>
        <ButtonGroup pb={10}>
          <Button
            mt={8}
            colorScheme={'orange'}
            rounded='full'
            onClick={createPayment}
          >
            Upgrade Now
          </Button>
          <Link to={'/banks'}>
            <Button mt={8} colorScheme={'orange'} rounded='full'>
              Bank Details
            </Button>
          </Link>
        </ButtonGroup>
      </Box>
    </Box>
  )
}

export default Upgrade
