import React, { useEffect, useState } from 'react'
import {
  Box,
  Heading,
  HStack,
  Spacer,
  Button,
  Text,
  Center,
} from '@chakra-ui/react'
import { Table, Thead, Tbody, Tag, Tr, Th, Td } from '@chakra-ui/react'
import useFetch from 'use-http'
import { format } from 'date-fns'
import { FiArrowLeft } from 'react-icons/fi'
import { Link } from 'react-router-dom'

const STATUS: any = {
  failure: {
    bg: '#FDE5E6',
    text: '#DB262D',
  },
  success: {
    bg: '#CDF8D9',
    text: '#259645',
  },
}

function Transaction() {
  const api = useFetch('/api/listing/payment_history')
  const [transactions, setTransactions] = useState<any[]>([])
  const [page, setPage] = useState(1)

  useEffect(() => {
    api.post({ page }).then((data) => {
      setTransactions([...transactions, ...data.data])
    })

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page])

  if (transactions.length === 0) {
    return (
      <Center py={8}>
        <Text fontWeight={'bold'}>No Transactions</Text>
      </Center>
    )
  }

  return (
    <Box p={8}>
      <HStack>
        <Text fontSize={{ base: '2xl', md: '4xl' }}>
          <FiArrowLeft />
        </Text>

        <Text fontWeight={'bold'} fontSize={{ base: 'md', md: '3xl' }}>
          Transaction History
        </Text>
        <Spacer />
        <Link to={'/payments'}>
          <Button colorScheme={'orange'} rounded='full'>
            Add Payment
          </Button>
        </Link>
      </HStack>
      <Table
        overflow={'auto'}
        minW={'400px'}
        mt={24}
        variant='simple'
        cellPadding={12}
      >
        <Thead>
          <Tr>
            <Th>DATE</Th>
            <Th>NARRATION</Th>
            <Th>AMOUNT</Th>
            <Th>STATUS</Th>
          </Tr>
        </Thead>
        <Tbody>
          {transactions.map((transaction: any) => {
            const { transaction_type } = transaction
            const _status = STATUS[transaction_type]
            return (
              <Tr>
                <Td>{format(new Date(transaction.transaction_date), 'PPP')}</Td>
                <Td>{transaction.remarks}</Td>
                <Td>
                  <b>{transaction.amount / 100}</b>
                </Td>
                <Td>
                  <Tag
                    color={_status ? _status['text'] : ''}
                    bg={_status ? _status['bg'] : ''}
                  >
                    {transaction_type}
                  </Tag>
                </Td>
              </Tr>
            )
          })}
        </Tbody>
      </Table>
      <Button
        onClick={() => {
          setPage(page + 1)
        }}
        mt={4}
      >
        Load more
      </Button>
    </Box>
  )
}

export default Transaction
