import React from 'react'
import { Box, Text, Heading, Circle, HStack } from '@chakra-ui/react'

type CardProps = {
  isSelected?: boolean
  title: string
  onSelect: () => void
  condition?: any
}

const ConditionCard: React.FC<CardProps> = ({
  title,
  isSelected,
  onSelect,
  condition,
}) => {
  return (
    <Box
      onClick={onSelect}
      minW='180px'
      p={4}
      borderWidth='1px'
      rounded='xl'
      cursor='pointer'
      transition='all 0.2s'
      _hover={{ shadow: 'lg' }}
      userSelect='none'
      pos='relative'
    >
      <HStack>
        <Box
          p={1}
          borderWidth={'1px'}
          rounded='full'
          borderColor={isSelected ? 'red.500' : 'gray.400'}
        >
          <Circle w='12px' h='12px' bg={isSelected ? 'red.500' : 'white'} />
        </Box>

        <Text roundedTop='xl' py={2} fontWeight='semibold'>
          {title}
        </Text>
      </HStack>
      <Box>
        <Text textTransform={'capitalize'}>Indicative Price</Text>
        <Heading size='md'>
          {Number(condition.price).toLocaleString()} /-
        </Heading>
      </Box>
    </Box>
  )
}

export default ConditionCard
