import { CachePolicies, IncomingOptions } from 'use-http'
// import { enc_AES256, dec_objAES256 } from './encryption'

const options: IncomingOptions = {
  cachePolicy: CachePolicies.NO_CACHE,
  interceptors: {
    request: ({ options }) => {
      let body: string | any = String(options.body || '{}')
      const jwt = localStorage.getItem('user-token')
      if (jwt) {
        Object.assign(options.headers, {
          Authorization: `Bearer ${jwt}`,
        })
      }
      // body = JSON.parse(body)

      // Object.keys(body).forEach((key) => {
      //   const value = body[key]
      //   body[key] = enc_AES256(value)
      // })

      // body = JSON.stringify(body)
      const final = { ...options, body }
      return final
    },
    response: async ({ response }) => {
      // let { data } = response
      // if (data) {
      //   const _data = data.data
      //   Object.assign(data, { data: dec_objAES256(_data) })
      // }
      return response
    },
  },
}

export default options
