import React, { useEffect, useMemo, useState } from 'react'
import { Box, Heading, HStack, Text, Spacer } from '@chakra-ui/layout'
import { Button, IconButton } from '@chakra-ui/button'
import { FiArrowLeft } from 'react-icons/fi'
import BuyCoalStep1 from '../components/buy-steps/1'
import { BuyCoalStore } from '../store/BuyCoal'
import useFetch from 'use-http'
import { UserStore } from '../store/user'
import {
  Spinner,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
} from '@chakra-ui/react'
import { Link } from 'react-router-dom'
import { TrailDataStore } from '../store/TrialData'

const BuyCoal: React.FC = () => {
  const [steps, setSteps] = useState({
    current: 1,
    loading: false,
  })
  const { user } = UserStore.useState()
  const trialData = TrailDataStore.useState((s) => s.data)
  const orderApi = useFetch('/api/add/order')
  const buyCoalStore = BuyCoalStore.useState()
  const trialApi = useFetch('/api/listing/trial_data')
  const startTrialApi = useFetch('/api/add/activate_trial')
  const successModal = useDisclosure()

  const startTrial = () => {
    startTrialApi.post().then((res) => {
      setTimeout(() => {
        window.location.href = '/buy-coal'
      }, 1000)
    })
  }

  const Continue = () => {
    orderApi.post({ ...buyCoalStore.data }).then((res) => {
      if (res.message === 'Success') {
        successModal.onOpen()
      }
    })
  }

  const PrevStep = () => {
    setSteps({ ...steps, current: steps.current - 1 })
  }

  return (
    <Box m='0 auto' maxW='3xl' p={{ base: 6, lg: 0 }}>
      <HStack py={4}>
        <Box
          fontSize={{ base: '2xl', lg: '4xl' }}
          onClick={() => {
            window.history.back()
          }}
          cursor={'pointer'}
        >
          <FiArrowLeft />
        </Box>
        <Box mt={4}>
          <Heading size={'lg'}>Buy Coal</Heading>
        </Box>
      </HStack>

      <HStack my={4}>
        {steps.current > 1 && (
          <IconButton
            variant='link'
            onClick={PrevStep}
            size='md'
            aria-label='back'
            icon={<FiArrowLeft />}
          />
        )}
        <Text fontSize='md' color='gray'>
          Please select the proper details given below, so that we can match you
          reqirement in given time. Buy coal at best price by selecting the
          proper details given below and enter your BID price.
        </Text>
      </HStack>

      <Box mb={4}>
        {trialApi.loading ? (
          <Spinner />
        ) : (
          <Box>
            <Box
              d={trialData.paid_member && !trialData.trail ? 'none' : 'block'}
            >
              <HStack
                d={trialData.trial ? 'flex' : 'none'}
                rounded='xl'
                bg={'#ED1C241A'}
                p={4}
              >
                <Box>
                  <Heading size='sm'>
                    {trialData.trial_type === 'activated'
                      ? 'TRIAL ACTIVATED'
                      : trialData.trial_type === 'expired'
                      ? 'TRIAL EXPIRED'
                      : 'ACTIVATE MY TRAIL'}
                  </Heading>
                </Box>
                <Spacer />
                <HStack
                  d={trialData.trial_type === 'expired' ? 'none' : 'flex'}
                >
                  <Text
                    display={trialData.trial_remaining > 0 ? 'flex' : 'none'}
                  >
                    BIDS LEFT: {trialData?.trial_remaining}
                  </Text>
                  <Button
                    display={
                      trialData.trial_type !== 'not activated' ? 'none' : 'flex'
                    }
                    colorScheme={'red'}
                    rounded='full'
                    onClick={startTrial}
                  >
                    ACTIVATE
                  </Button>
                </HStack>
              </HStack>

              <Box my={4} bg='orange.500' p={4} rounded='xl' color='white'>
                <Heading size='md'>UPGRADE MY ACCOUNT</Heading>
                <Text mt={3}>
                  You are using basic membership, to buy coal, you need to
                  upgrade to premium
                </Text>
                <Box pb={2}>
                  <Link to={'/upgrade'}>
                    <Button color='black' mt={4} size='lg'>
                      UPGRADE NOW
                    </Button>
                  </Link>
                </Box>
              </Box>
            </Box>
            {!trialData?.paid_membership_required ||
            user?.paid_membership ||
            trialData?.trial_remaining > 0 ? (
              <>
                <BuyCoalStep1 continue={Continue} />
                {/* <Button
                  mt={4}
                  isLoading={orderApi.loading}
                  rounded='full'
                  colorScheme='brand'
                  onClick={Continue}
                >
                  Bid Offer
                </Button> */}
              </>
            ) : (
              <></>
            )}
          </Box>
        )}
      </Box>
      <Box>
        <Modal
          isOpen={successModal.isOpen}
          closeOnEsc={false}
          closeOnOverlayClick={false}
          onClose={() => {
            window.location.href = '/orders'
          }}
        >
          <ModalOverlay />
          <ModalContent>
            <ModalHeader>Success</ModalHeader>
            <ModalCloseButton />
            <ModalBody pb={6}>
              <Text>Your bid has been placed successfully</Text>
            </ModalBody>
          </ModalContent>
        </Modal>
      </Box>
    </Box>
  )
}

export default BuyCoal
