import React, { useEffect, useState } from 'react'
import {
  Box,
  Heading,
  SimpleGrid,
  Stack,
  HStack,
  Text,
  PinInput,
  PinInputField,
  Center,
  FormControl,
  FormLabel,
  Button,
} from '@chakra-ui/react'
import useFetch from 'use-http'
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'

const Login: React.FC = () => {
  const [otp, setOtp] = useState(false)
  const loginApi = useFetch('/login')
  const [otpValue, setOtpValue] = useState('')
  const [isNewUser, setIsNewUser] = useState(false)
  const [phone, setPhone] = useState('')
  const [invalidOtp, setInvalidOtp] = useState(false)
  const [timer, setTimer] = useState(40)

  const login = () => {
    if (phone) {
      loginApi.post({ phone }).then((data) => {
        if (data?.data) {
          setOtp(true)
          loginApi.post('/sendOTP', {
            phone,
          })
          if (data.data.type === 'new') {
            setIsNewUser(true)
          }
        }
      })
    }
  }
  const countdown = () => {
    const i = setInterval(() => {
      setTimer((timer) => {
        if (timer === 1) {
          clearInterval(i)
        }
        return timer - 1
      })
    }, 1000)
  }

  useEffect(() => {
    countdown()
  }, [])

  const submitOtp = (value: any) => {
    setInvalidOtp(false)
    loginApi.post('/verifyOTP', { phone, otp: value }).then((data) => {
      if (data?.message === 'error') {
        setInvalidOtp(true)
        setOtpValue('')
        return
      }
      if (data?.token) {
        localStorage.setItem('user-token', data.token)
      }

      if (data?.registration_completed === 'no') {
        window.location.href = `/register`
        return
      }

      window.location.href = `/`
    })
  }

  return (
    <Box m='0 auto' maxW='6xl' mt={{ base: 2, xl: 14 }}>
      <SimpleGrid columns={{ base: 1, xl: 2 }} m={4} minH={'450px'}>
        <Box px={{ base: 4, xl: 24 }} py={{ base: 12, xl: 24 }} rounded='lg'>
          <Heading size='3xl'>Anmol Coal</Heading>
          <Text mt={4} color='gray.600'>
            Login / Signup to enter your own BID price for purchasing COAL and
            get the best deal upon approval
          </Text>
        </Box>
        <Box
          ml={{ base: 0, xl: 12 }}
          px={{ base: 4, xl: 24 }}
          py={{ base: 2, xl: 24 }}
        >
          <Stack spacing={4}>
            <Box d={!otp ? 'block' : 'none'}>
              <Heading size='lg'>Login/ Signup</Heading>
              <FormControl mt={4}>
                <FormLabel>Phone Number</FormLabel>
                <PhoneInput
                  country={'in'}
                  inputStyle={{
                    width: '100%',
                  }}
                  inputProps={{
                    name: 'phone',
                    required: true,
                    autoFocus: true,
                  }}
                  onChange={(v) => setPhone(v)}
                />
              </FormControl>
            </Box>
            <Box d={otp ? 'block' : 'none'}>
              <Center>
                <Box>
                  <HStack mt={6}>
                    <PinInput
                      value={otpValue}
                      onChange={(v) => {
                        setOtpValue(v)
                        if (v.length === 4) {
                          submitOtp(v)
                        }
                      }}
                    >
                      <PinInputField borderColor={'black'} />
                      <PinInputField borderColor={'black'} />
                      <PinInputField borderColor={'black'} />
                      <PinInputField borderColor={'black'} />
                    </PinInput>
                  </HStack>
                  <Text mt={4} textAlign={'center'}>
                    Enter the OTP
                  </Text>
                  <Center>
                    {timer === 0 ? (
                      <Button
                        onClick={() => {
                          setTimer(40)
                          countdown()
                          loginApi.post('/sendOTP', {
                            phone,
                          })
                        }}
                        mt={4}
                      >
                        Resend OTP
                      </Button>
                    ) : (
                      <Text mt={4}>
                        Resend in 00:{timer < 10 ? '0' : ''}
                        {timer}
                      </Text>
                    )}
                  </Center>
                  {invalidOtp && (
                    <Text mt={4} color='red.500'>
                      Invalid OTP
                    </Text>
                  )}
                </Box>
              </Center>
            </Box>
            <Button onClick={login} d={!otp ? 'block' : 'none'}>
              Continue
            </Button>
          </Stack>
        </Box>
      </SimpleGrid>
    </Box>
  )
}

export default Login
