import { Box, Heading, HStack, Image, SimpleGrid, Text } from '@chakra-ui/react'

const StepComponent = ({ step, body, title }) => {
  return (
    <>
      <Box
        d={{ base: 'none', lg: 'block' }}
        p={6}
        borderWidth='1px'
        borderColor='#DEDEDE'
        rounded='md'
      >
        <Text fontSize={12} fontWeight='600' color='#696969'>
          STEP {step}
        </Text>
        <HStack mt={2} alignItems={'flex-start'} spacing={4}>
          <Box>
            <Image w={'16'} src={`/assets/steps/${step}.png`} />
          </Box>
          <Box>
            <Heading textTransform={'uppercase'} size='xs'>
              {title}
            </Heading>
            <Text mt={2} textColor={'gray.500'}>
              {body}
            </Text>
          </Box>
        </HStack>
      </Box>

      <Box
        d={{ base: 'block', lg: 'none' }}
        p={6}
        borderWidth='1px'
        borderColor='#DEDEDE'
        rounded='md'
      >
        <Text fontSize={14} fontWeight='600' color='#696969'>
          STEP {step}
        </Text>
        <HStack
          d={{ base: 'block', lg: 'none' }}
          mt={2}
          alignItems={'flex-start'}
          spacing={4}
        >
          <Box px={24} mt={4}>
            <Image w={'16'} src={`/assets/steps/${step}.png`} />
          </Box>
          <Box>
            <Heading
              mt={4}
              textAlign={'center'}
              textTransform={'uppercase'}
              size='xs'
            >
              {title}
            </Heading>
            <Text mt={2} textAlign={'center'} textColor={'gray.500'}>
              {body}
            </Text>
          </Box>
        </HStack>
      </Box>
    </>
  )
}

const Steps = () => {
  return (
    <Box p={8}>
      <Heading mb={6}>How it works?</Heading>
      <SimpleGrid columns={{ base: 1, lg: 3 }} spacing={4}>
        <StepComponent
          title={'select coal'}
          body="Choose the kind of coal you'd want to purchase."
          step={1}
        />
        <StepComponent
          title={'coal market price'}
          body="The market price of the coal you've chosen will be displayed."
          step={2}
        />
        <StepComponent
          title={'BID YOUR PRICE'}
          body='Enter your fair price, which can be more/less or equal to the market price'
          step={3}
        />
        <StepComponent
          title={'ORDER CONFiRMATION'}
          body='After submitting your order, wait for the confirmation.'
          step={4}
        />
        <StepComponent
          title={'order acceptence'}
          body='Based on your bid price, your order will be accepted or rejected'
          step={5}
        />
        <StepComponent
          title={'bid your best'}
          body='Everyday limited quantity of coal will be sold to Best Bidders.'
          step={6}
        />
      </SimpleGrid>
    </Box>
  )
}

export default Steps
