import {
  Box,
  Button,
  Center,
  chakra,
  Heading,
  HStack,
  Spacer,
  Text,
} from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import useFetch from 'use-http'
import { BsArrowLeftCircle, BsArrowRightCircle } from 'react-icons/bs'
import { statusColor } from '../../constants/status-colors'
import { Link } from 'react-router-dom'
import { UserStore } from '../../store/user'

const HomeOrders = () => {
  const [orders, setOrders] = useState<any[]>([])
  const orderApi = useFetch('/api/listing/latest_orders')
  const orderDetailApi = useFetch('/api/listing/order_details')
  const [active, setActive] = useState(0)

  const { user } = UserStore.useState()
  useEffect(() => {
    if (user)
      orderApi.post().then((res) => {
        setOrders(res.data)
      })
  }, [user])

  if (orders.length === 0) {
    return (
      <Center>
        <Text py={6} fontWeight={'bold'}>
          No Orders
        </Text>
      </Center>
    )
  }

  return (
    <Box p={8}>
      <HStack>
        <Heading>Orders</Heading>
        <Spacer />
        <Link to={'/orders'}>
          <Text fontWeight={'bold'}>View All</Text>
        </Link>
      </HStack>
      <HStack
        mt={4}
        overflow={'auto'}
        spacing={6}
        d={{ base: 'none', lg: 'flex' }}
      >
        {orders.map((order, idx) => {
          const sc = statusColor[order.order_status]

          return (
            <Box
              key={idx}
              borderWidth={'1px'}
              rounded={'lg'}
              shadow={'sm'}
              p={2}
              minW={'sm'}
            >
              <HStack>
                <Text> ORDER ID: #{order.order_id}</Text>
                <Spacer />
                <Box borderWidth={'1px'} p='1' rounded={'full'}>
                  <Text fontSize={'xs'} textColor={'black'}>
                    STATUS :{' '}
                    <chakra.span
                      p={'1'}
                      textColor={sc[0]}
                      rounded={'full'}
                      bg={sc[1]}
                    >
                      {order.order_status}
                    </chakra.span>
                  </Text>
                </Box>
              </HStack>
              <Box mt={2}>
                <Text fontWeight={'bold'}> {order.coal_name}</Text>
              </Box>
              <Box mt={2}>
                <HStack>
                  <Text>Quantity: {order.quantity}</Text>
                  <Box
                    h={'4px'}
                    w={'4px'}
                    rounded={'full'}
                    bg={'gray.500'}
                  ></Box>
                  <Text>Port: {order.port_name}</Text>
                </HStack>
              </Box>
              <Link to={`/orders?id=${order.order_id}`}>
                <Box mt={2}>
                  <Button
                    fontSize={'xs'}
                    rounded={'full'}
                    colorScheme={'yellow'}
                  >
                    View Order Details
                  </Button>
                </Box>
              </Link>
            </Box>
          )
        })}
      </HStack>
      <HStack
        mt={4}
        overflow={'auto'}
        spacing={6}
        d={{ base: 'flex', lg: 'none' }}
      >
        {orders.map((order, idx) => {
          const sc = statusColor[order.order_status]

          return (
            <Box
              key={idx}
              borderWidth={'1px'}
              rounded={'lg'}
              shadow={'sm'}
              p={2}
              minW={64}
              overflow={'auto'}
            >
              <Text> #{order.order_id}</Text>

              <Box mt={2}>
                <Text fontWeight={'bold'}> {order.coal_name}</Text>
              </Box>
              <Box mt={4}>
                <Text fontSize={'xs'} textColor={'black'}>
                  STATUS :{' '}
                  <chakra.span
                    p={'1'}
                    textColor={sc[0]}
                    rounded={'full'}
                    bg={sc[1]}
                  >
                    {order.order_status}
                  </chakra.span>
                </Text>
              </Box>
              <Box mr={6} px={2} mt={4}>
                <Box py={2} rounded={'full'} bg={'#FEF3E5'}>
                  <Center>
                    <Link to={`/orders?id=${order.order_id}`}>
                      <Text
                        fontSize={'sm'}
                        textColor={'#FCB563'}
                        cursor={'pointer'}
                        fontWeight={'bold'}
                      >
                        VIEW ORDER DETAILS
                      </Text>
                    </Link>
                  </Center>
                </Box>
              </Box>
            </Box>
          )
        })}
      </HStack>
    </Box>
  )
}

export default HomeOrders
