import React from 'react'
import { Box, HStack, Stack, Text } from '@chakra-ui/react'

type Props = {
  data: any
}

const exclude = ['id']

const ConditionInfo: React.FC<Props> = ({ data }) => {
  const keys = Object.keys(data)

  return (
    <Stack space={4}>
      {keys.map((key, index) => {
        const value = data[key]
        if (exclude.includes(key)) return null

        if (!value || value.length < 2) return null

        return (
          <HStack alignItems={'flex-start'} key={index}>
            <Box mt={3}>
              <Box h='6px' w='6px' rounded='full' bg='#696969' />
            </Box>
            <Box ml={2}>
              <Text color='#696969' fontSize={'lg'} textTransform='capitalize'>
                {key.replace(/_/g, ' ')}
              </Text>
              <Text mt={1} fontSize={'lg'}>
                {value}
              </Text>
            </Box>
          </HStack>
        )
      })}
    </Stack>
  )
}

export default ConditionInfo
