import React, { useMemo, useRef, useState } from 'react'
import { useDisclosure } from '@chakra-ui/hooks'
import { Box, Flex, Spacer, Text } from '@chakra-ui/layout'
import { IconButton } from '@chakra-ui/button'
import {
  Drawer,
  DrawerOverlay,
  DrawerContent,
  Image,
  Avatar,
  HStack,
  Badge,
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  Stack,
  Center,
  Heading,
  Divider,
} from '@chakra-ui/react'
import { FiArrowLeft, FiMenu, FiPlus, FiX } from 'react-icons/fi'
import { useLocation } from 'react-router'
import { Link } from 'react-router-dom'
import { UserStore } from '../store/user'
import {
  FiCreditCard,
  FiHome,
  FiPhone,
  FiShoppingCart,
  FiUser,
} from 'react-icons/fi'
import useFetch from 'use-http'

export type HeaderProps = {}

const Navigation = ({
  path,
  isPhone = false,
  close = () => null,
}: {
  path: string
  isPhone?: boolean
  close?: () => void
}) => {
  const { user } = UserStore.useState()
  const registrationCompleted = user?.registration_completed

  const HEADER_ITEMS = useMemo(
    () => [
      {
        title: 'Home',
        path: '/',
        icon: FiHome,
      },
      user &&
        registrationCompleted && {
          title: 'Buy Coal',
          path: '/buy-coal',
          icon: FiCreditCard,
        },
      user &&
        registrationCompleted && {
          title: 'My Order',
          path: '/orders',
          icon: FiShoppingCart,
        },

      !user
        ? {
            title: 'Login',
            path: '/login',
            icon: FiUser,
          }
        : null,
      user &&
        registrationCompleted && {
          title: 'Payments',
          path: '/payments',
          icon: () => null,
        },
      user &&
        registrationCompleted && {
          title: 'Transactions',
          path: '/transactions',
          icon: FiUser,
        },
      {
        title: 'Contact us',
        path: '/contact',
        icon: FiPhone,
      },
    ],
    [user]
  )
  const [alert, setAlert] = useState<any>({
    visible: false,
  })
  const isProfileActive = useMemo(() => path === '/profile', [path])
  const cancelApi = useFetch('/api/add/cancel_subscription_request')
  const drawer = useDisclosure()

  const cancelRequest = () => {
    cancelApi.post().then((res) => {
      setAlert({
        visible: true,
        message:
          res.message === 'success'
            ? 'Your request has been sent'
            : 'You already have a pending request',
      })
    })
  }

  return (
    <Box
      d={isPhone ? 'unset' : 'flex'}
      alignItems='center'
      userSelect='none'
      p={isPhone ? 12 : 0}
    >
      {HEADER_ITEMS.map((item, idx) => {
        if (!item) return null

        const isActive = path === item.path

        return (
          <Link to={item.path} key={idx} onClick={close}>
            <Box
              mr={4}
              d='flex'
              alignItems='center'
              cursor='pointer'
              _hover={{
                opacity: !isActive && 0.8,
              }}
              transition='all 0.2s'
              borderBottomColor={isActive ? 'red' : 'black'}
              borderBottomWidth='4px'
              transform={isActive ? 'scale(1.01)' : 'scale(1)'}
              willChange='transform'
            >
              <Text
                mx={2}
                py={4}
                color='#EDEDED'
                fontWeight={isActive ? 'semibold' : 'normal'}
              >
                {item.title}
              </Text>
            </Box>
          </Link>
        )
      })}
      {user && (
        <Box
          onClick={() => {
            drawer.onOpen()
          }}
          cursor={'pointer'}
        >
          <Avatar size={'md'} />
        </Box>
      )}
      <Box>
        <Drawer
          isOpen={drawer.isOpen}
          placement='right'
          onClose={() => {
            drawer.onClose()
          }}
          size={'md'}
        >
          <DrawerOverlay />
          <DrawerContent>
            <HStack p={4} spacing={6} pb={4}>
              <Box
                fontSize={'4xl'}
                onClick={() => {
                  drawer.onClose()
                }}
                cursor={'pointer'}
              >
                <FiArrowLeft />
              </Box>
              <Text fontWeight={'bold'} fontSize={{ base: '2xl', lg: '3xl' }}>
                My Profile
              </Text>
            </HStack>
            <Box mt={4} p={4}>
              <Box>
                <Avatar size={'lg'} />
              </Box>
              <Box mt={4}>
                <Text fontWeight={'bold'} fontSize={'md'}>
                  {user?.name}
                </Text>
              </Box>
              <Box mt={4}>
                <HStack>
                  <HStack>
                    <Text fontSize={'sm'} textColor={'gray.500'}>
                      Account Type:
                    </Text>
                    <Badge
                      bg={user?.paid_membership ? 'orange.200' : 'green.200'}
                    >
                      {user?.paid_membership ? 'Premium' : 'Basic'}
                    </Badge>
                  </HStack>
                  <HStack>
                    <Text fontSize={'sm'} textColor={'gray.500'}>
                      Contact
                    </Text>
                    <Badge fontSize={'sm'}>{user?.phone}</Badge>
                  </HStack>
                </HStack>
              </Box>
            </Box>
            <Box p={4} mt={2}>
              <Stack spacing={8} cursor={'pointer'}>
                <Link
                  to={'/companies'}
                  onClick={() => {
                    drawer.onClose()
                  }}
                >
                  <Heading size={'md'}>Company</Heading>
                </Link>
                {user?.paid_membership ? <Divider my={8} /> : null}
                {user?.paid_membership ? (
                  <Text
                    fontWeight={'bold'}
                    textColor={'red.500'}
                    onClick={cancelRequest}
                  >
                    CANCEL SUBSCRIPTION
                  </Text>
                ) : null}
                <Divider my={8} />
                <Heading
                  fontWeight={'bold'}
                  size={'md'}
                  onClick={() => {
                    localStorage.removeItem('user-token')
                    setTimeout(() => {
                      window.location.reload()
                    }, 800)
                  }}
                >
                  Sign Out
                </Heading>
              </Stack>
            </Box>
          </DrawerContent>
        </Drawer>
      </Box>
      <Box>
        <Modal
          isOpen={alert.visible}
          onClose={() => {
            setAlert({
              visible: false,
            })
          }}
        >
          <ModalOverlay />
          <ModalContent>
            <ModalHeader>Modal Title</ModalHeader>

            <ModalBody>
              {alert.message}
              <Center>
                <Box mt={4}>
                  <Button
                    onClick={() => {
                      setAlert({
                        visible: false,
                      })
                    }}
                    colorScheme={'red'}
                  >
                    Confirm
                  </Button>
                </Box>
              </Center>
            </ModalBody>
          </ModalContent>
        </Modal>
      </Box>
    </Box>
  )
}

const Header: React.FC<HeaderProps> = () => {
  const focusRef = useRef(null)
  const location = useLocation()
  const { isOpen, onOpen, onClose } = useDisclosure()

  return (
    <Flex
      shadow='base'
      bg='black'
      px={{ base: 2, md: 24 }}
      alignItems='center'
      py={{ base: 2, md: 4 }}
    >
      <Image p={4} w={44} src={'/assets/images/anmol-white-logo.png'} />
      <Spacer />
      <IconButton
        mr={4}
        d={{ base: 'flex', md: 'none' }}
        aria-label='menu'
        size='md'
        icon={<FiMenu />}
        onClick={onOpen}
      />
      <Box d={{ base: 'none', md: 'flex' }}>
        <Navigation path={location.pathname} />
      </Box>
      <Drawer
        isOpen={isOpen}
        placement='left'
        onClose={onClose}
        finalFocusRef={focusRef}
      >
        <DrawerOverlay />
        <DrawerContent maxW='xs' bg='black'>
          <Navigation
            close={() => {
              setTimeout(onClose, 300)
            }}
            isPhone
            path={location.pathname}
          />
        </DrawerContent>
      </Drawer>
    </Flex>
  )
}
export default Header
