import {
  Drawer,
  DrawerBody,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  HStack,
  Box,
  Text,
  Stack,
  Divider,
} from '@chakra-ui/react'
import { format } from 'date-fns'

const OrderTimeline = ({ isOpen, onClose, remarks = [] }) => {
  return (
    <div>
      <Drawer size='lg' isOpen={isOpen} placement='right' onClose={onClose}>
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Timeline</DrawerHeader>

          <DrawerBody>
            <Stack spacing={4}>
              {remarks?.map((remark: any, index) => (
                <>
                  <HStack alignItems={'flex-start'} spacing={4}>
                    <Box h='40px' w='40px' rounded='full' bg='orange.300' />
                    <Box>
                      <Text fontSize={'xl'} mb={2}>
                        {remark.latest_status}
                      </Text>
                      <p>{remark.remark}</p>
                      <Text fontSize={'sm'} color='gray.400'>
                        {/* {format(new Date(remark.remark_time), 'PPP HH:mm')} */}
                      </Text>
                    </Box>
                  </HStack>
                  {index + 1 < remarks.length && <Divider />}
                </>
              ))}
            </Stack>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </div>
  )
}

export default OrderTimeline
