import {
  Box,
  Text,
  Stack,
  HStack,
  Spacer,
  Divider,
  Badge,
} from '@chakra-ui/layout'
import { format } from 'date-fns'
import { FiChevronRight } from 'react-icons/fi'

type Props = {
  active?: any
  orders: any[]
  onChange: (type: string) => void
}

const MyOrderSidebar: React.FC<Props> = ({ active, onChange, orders }) => {
  return (
    <Stack spacing={6} maxH='600px' overflow={'auto'}>
      {orders.map((order) => {
        return (
          <>
            <HStack
              cursor={'pointer'}
              p={3}
              borderWidth={active ? '1px' : 0}
              rounded={'lg'}
              borderColor={active === order.order_id ? '#ED1C24' : 'white'}
              bg='#FBF9F9'
              onClick={() => onChange(order)}
            >
              <Box>
                <Text fontWeight={'semibold'} fontSize={'sm'} color='#909090'>
                  #{order.order_id}  {order.order_status}
                </Text>
                <Text color='#909090' mt={2}>
                  Coal Type
                </Text>
                <Text fontSize={'xl'}>{order.coal_name}</Text>
                <Badge rounded='full'>
                  {format(new Date(order.order_time), 'PPP HH:mm')}
                </Badge>
              </Box>
              <Spacer />
              <Box>
                <Text fontSize={'2xl'}>
                  <FiChevronRight />
                </Text>
              </Box>
            </HStack>
            <Divider color='#DEDEDE' />
          </>
        )
      })}
    </Stack>
  )
}

export default MyOrderSidebar
