import React, { useEffect } from 'react'
import { Box, Center, Heading, Spinner, Text } from '@chakra-ui/react'
import OpenDeeplink from '../components/OpenDeeplink'

const FetchApp = () => {
  const isAndroid = navigator.userAgent.includes('Android')

  const myInitialURL = window.location.pathname
  const downloadLink = isAndroid
    ? 'https://play.google.com/store/apps/details?id=com.anmolindialtd.anmolindialimited'
    : 'https://apps.apple.com/in/app/anmol-coal/id1055161589'
  const deeplink = `anmolcoal://app/app/fetch/${myInitialURL.split('/')[3]}`

  useEffect(() => {
    window.open(deeplink, '_self')
    setTimeout(() => {
      window.open(downloadLink, '_self')
    }, 1000)
  }, [myInitialURL])

  return (
    <>
      <Center gridGap={50} h={'80vh'}>
        <Heading textAlign={'center'}>Redirecting to Play Store</Heading>
        <Spinner size={'xl'} colorScheme={'orange'} />
      </Center>
    </>
  )
}

export default FetchApp
