import React, { useEffect, useMemo, useState } from 'react'

import { Switch, Route, useLocation, Redirect } from 'react-router-dom'
import Layout from './components/layout'

import Login from './pages/auth/Login'
import Profile from './pages/Profile'
import Home from './pages/Home'
import News from './pages/News'
import BuyCoal from './pages/BuyCoal'
import Payment from './pages/Payment'
import MyOrders from './pages/MyOrders'
import Register from './pages/auth/Register'
import Transaction from './pages/Transaction'
import Bank from './pages/Bank'

import '@fontsource/inter/400.css'
import '@fontsource/inter/700.css'
import { UserStore } from './store/user'
import Contact from './pages/Contact'
import Upgrade from './pages/Upgrade'
import useFetch from 'use-http'
import { Box, Center, Spinner } from '@chakra-ui/react'
import { TrailDataStore } from './store/TrialData'
import Companies from './pages/companies'
import FetchApp from './pages/FetchApp'
import OpenDeeplink from './components/OpenDeeplink'

const App: React.FC = () => {
  const { user } = UserStore.useState()
  const isAuth = useMemo(() => !!user, [user])
  const trialApi = useFetch('/api/listing/trial_data')
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    trialApi.post().then((res) => {
      TrailDataStore.update((s) => {
        s.data = res.data
        setLoading(false)
      })
    })
  }, [])

  if (loading) {
    return (
      <Box mt={6}>
        <Center>
          <Spinner size='sm' />
        </Center>
      </Box>
    )
  }

  return (
    <Layout>
      <Switch>
        <Route path='/' exact strict component={Home} />
        <Route path='/orders' exact component={!isAuth ? Login : MyOrders} />
        <Route path='/payments' exact component={!isAuth ? Login : Payment} />
        <Route
          path='/transactions'
          exact
          component={!isAuth ? Login : Transaction}
        />
        <Route path='/app/fetch/:id' exact component={FetchApp} />
        <Route path='/buy-coal' component={!isAuth ? Login : BuyCoal} />
        <Route path='/news' component={News} />
        <Route path='/profile' component={!isAuth ? Login : Profile} />
        <Route
          path='/login'
          component={!isAuth ? Login : () => <Redirect to='/' />}
        />
        <Route path='/contact' component={Contact} />
        <Route path='/register' component={Register} />
        <Route path='/upgrade' component={Upgrade} />
        <Route path='/banks' component={Bank} />
        <Route path='/companies' component={Companies} />
      </Switch>
    </Layout>
  )
}

export default App
