import {
  Box,
  Center,
  Heading,
  HStack,
  Spinner,
  Stack,
  Text,
} from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import { FiArrowLeft } from 'react-icons/fi'
import useFetch from 'use-http'

const Contact = () => {
  const [contact, setContact] = useState<any[]>([])
  const contactApi = useFetch('/api/listing/contact')

  useEffect(() => {
    contactApi.post().then((res) => {
      setContact(res.data)
      // console.log('contact data', res.data)
    })
  }, [])

  return (
    <Box px={7} py={14}>
      <HStack mb={6}>
        <Text
          fontSize={{ base: '2xl', lg: '4xl' }}
          onClick={() => {
            window.history.back()
          }}
          cursor={'pointer'}
        >
          <FiArrowLeft />
        </Text>

        <Heading size={'lg'}>Contact us</Heading>
      </HStack>

      {contactApi.loading ? (
        <Center>
          <Spinner mt={6} />
        </Center>
      ) : (
        contact.map((contact, idx) => (
          <Box key={idx} mb={6}>
            <Heading size='md'>{contact.type}</Heading>
            <Stack mt={4} space={3}>
              {Object.keys(contact.details).map((key, idx) => (
                <Box key={idx}>
                  <Text
                    color='brand.400'
                    fontWeight='semibold'
                    textTransform={'capitalize'}
                  >
                    {key}
                  </Text>
                  <Text
                    cursor={'pointer'}
                    onClick={() => {
                      const protocol = key.includes('email')
                        ? 'mailto'
                        : key.includes('phone') || key.includes('mobile')
                        ? 'tel'
                        : ''
                      if (protocol)
                        window.open(`${protocol}:${contact.details[key]}`)
                    }}
                  >
                    {contact.details[key]}
                  </Text>
                </Box>
              ))}
            </Stack>
          </Box>
        ))
      )}
    </Box>
  )
}

export default Contact
