import useFetch from 'use-http'
import {
  Box,
  Button,
  chakra,
  Heading,
  HStack,
  Image,
  Input,
  Stack,
  Text,
} from '@chakra-ui/react'
import useRazorpay from 'react-razorpay'
import { UserStore } from '../store/user'
import { useEffect, useState } from 'react'
import { FiArrowLeft } from 'react-icons/fi'

const Payment = () => {
  const Razorpay = useRazorpay()
  const [amount, setAmount] = useState<any>('0')
  const [message, setMessage] = useState('')
  const paymentApi = useFetch('/api/add/createorder')
  const paymentVerifyApi = useFetch('/api/add/verifyAndStore')
  const userStore = UserStore.useState()
  const bankApi = useFetch('/api/listing/banks')
  const [banks, setBanks] = useState<any[]>([])

  const makePayment = () => {
    paymentApi
      .post({
        amountInPaise: amount * 100,
        type: 'deposit',
      })
      .then((data) => {
        if (!data.orderId) return

        const { orderId } = data

        const options = {
          key: 'rzp_live_W8oi4FR1FqKtxw', // Enter the Key ID generated from the Dashboard
          amount: amount.toString(),
          currency: 'INR',
          name: 'Anmol India Pvt.LTD.',
          description: 'Anmol India Pvt.LTD.',
          order_id: orderId,
          handler: async function (response: any) {
            paymentVerifyApi
              .post({
                ...response,
                user_id: userStore.data.user.id,
                amount,
                createOrderId: orderId,
                remarks: 'deposit',
              })
              .then((data) => {
                setMessage(data.message)
              })
          },

          notes: {
            type: 'deposit',
          },
        }

        const rzp1 = new Razorpay(options)

        rzp1.on('payment.failed', function (response: any) {
          setMessage(response.description)
        })

        rzp1.open()
      })
  }

  useEffect(() => {
    bankApi.post().then((res) => {
      setBanks(res.data)
    })
  }, [])

  return (
    <Box px={7} py={14}>
      <HStack spacing={6}>
        <Box
          fontSize={{ base: '2xl', lg: '4xl' }}
          cursor={'pointer'}
          onClick={() => {
            window.history.back()
          }}
        >
          <FiArrowLeft />
        </Box>
        <Box>
          <Text fontWeight={'bold'} fontSize={{ base: 'xl', md: '3xl' }}>
            Add Payment
          </Text>
        </Box>
      </HStack>

      <Box mt={4}>
        <Input
          maxW={'md'}
          onChange={(e) => {
            setAmount(e.target.value)
          }}
          placeholder='Enter Amount'
        />
      </Box>
      <Box mt={6}>
        <HStack>
          <Image w={'15px'} src='/assets/images/info.png' />
          <Text fontSize={{ base: 'sm', md: 'md' }}>
            Amount will be deposited to
            <chakra.span
              fontSize={{ base: 'sm', md: 'lg' }}
              textColor={'#FB8500'}
            >
              {' '}
              Anmol India Pvt. LTD
            </chakra.span>
          </Text>
        </HStack>
      </Box>
      <Box mt={16} px={{ base: 0, md: 32 }}>
        <Button
          w={'xs'}
          colorScheme={'orange'}
          rounded={'full'}
          textColor={'white'}
          onClick={makePayment}
        >
          Add
        </Button>
      </Box>
      <Box mt={8}>
        <HStack>
          <Box bg={'gray.300'} h={'0.4px'} w={'200px'}></Box>
          <Text>OR</Text>
          <Box bg={'gray.300'} h={'0.4px'} w={'200px'}></Box>
        </HStack>
      </Box>
      <Box mt={10}>
        <Heading textTransform={'uppercase'} size={'md'}>
          pay using rtgs
        </Heading>
      </Box>

      <Stack spacing={4} mt={6}>
        {banks.map((bank, idx) => {
          return (
            <Box key={idx}>
              <Heading size={'md'}>{bank.bank_name}</Heading>
              {bank.bank_details.split('\n').map((detail: any, idx: number) => {
                return (
                  <Text mt={2} key={idx}>
                    {detail}
                  </Text>
                )
              })}
            </Box>
          )
        })}
      </Stack>
    </Box>
  )
}

export default Payment
