import { useEffect, useMemo, useState } from 'react'
import {
  Box,
  Flex,
  Heading,
  Text,
  Stack,
  Spacer,
  HStack,
  Center,
} from '@chakra-ui/layout'
import MyOrderSidebar from '../components/my-order/sidebar'
import useFetch from 'use-http'
import { FiArrowLeft } from 'react-icons/fi'
import { format } from 'date-fns'
import { Button, Spinner, Tag, useDisclosure } from '@chakra-ui/react'
import OrderTimeline from '../components/my-order/order-timeline'
import { statusColor } from '../constants/status-colors'

const PER_PAGE = 2

const MyOrders = (props) => {
  const [active, setActive] = useState<any>({})
  const ordersApi = useFetch('/api/listing/order_summary')
  const orderDetailApi = useFetch('/api/listing/order_details')

  const [page, setPage] = useState(1)
  const [orders, setOrders] = useState<any[]>([])
  const drawer = useDisclosure()
  const [more, setMore] = useState(true)

  useEffect(() => {
    const params = new URLSearchParams(props.location.search)
    const orderId = params.get('id')
    const order = orders.find((o) => o.order_id == orderId)
    if (order) {
      setActive(order)
    }
  }, [orders])

  const loadOrders = () => {
    ordersApi
      .post({
        page,
      })
      .then((res) => {
        if (!res?.data) return
        const { data } = res
        setOrders([...orders, ...res.data])

        if (data.length < PER_PAGE) setMore(false)
      })
  }

  useEffect(() => {
    loadOrders()
  }, [page])

  const remarkData = useMemo(
    () => (active?.remarks?.length > 0 ? active?.remarks[0] : null),
    [active?.remarks]
  )

  const { condition_details } = active

  const conditionKeys = useMemo(
    () => Object.keys(active?.condition_details || {}),
    [active?.condition_details]
  )

  const status = useMemo(
    () =>
      statusColor[active?.order_status]
        ? statusColor[active?.order_status]
        : [],
    [active?.order_status]
  )

  const textColor = status[0]
  const backgroundColor = status[1]

  return (
    <Box px={12} mt={10}>
      <HStack>
        <Text
          fontSize={{ base: '2xl', lg: '4xl' }}
          onClick={() => {
            window.history.back()
          }}
          cursor={'pointer'}
        >
          <FiArrowLeft />
        </Text>
        <Heading size='lg'>My Orders</Heading>
      </HStack>
      <Flex mt={12}>
        <Box w='xl' d={{ base: active.id ? 'none' : 'block', lg: 'block' }}>
          <MyOrderSidebar
            orders={orders}
            active={active.order_id}
            onChange={(o: any) => {
              orderDetailApi
                .post({
                  id: o.id,
                })
                .then((res) => {
                  setActive(res.data)
                })
            }}
          />
          <Box py={8}>
            <Button
              onClick={() => {
                setPage(page + 1)
              }}
              isDisabled={!more}
            >
              Load more
            </Button>
          </Box>
        </Box>
        <Stack
          p={{ base: 0, lg: 4 }}
          bg='#F8F8F8'
          ml={{ base: 0, lg: 4 }}
          w={{ base: 'full', lg: 'full' }}
          mt={{ base: 0, md: 0 }}
          py={{ base: 8, lg: 12 }}
          rounded='lg'
          d={{ base: active.id ? 'flex' : 'none', lg: 'flex' }}
        >
          <Box
            fontSize={'2xl'}
            onClick={() => {
              setActive({})
            }}
            cursor={'pointer'}
            d={{ base: 'block', lg: 'none' }}
          >
            <FiArrowLeft />
          </Box>
          {orderDetailApi.loading ? (
            <Center>
              <Spinner />
            </Center>
          ) : (
            active.order_id && (
              <Stack bg='white' rounded='xl' spacing={0}>
                <Box
                  p={4}
                  roundedTop='lg'
                  borderWidth={'1px'}
                  borderColor={'#DEDEDE'}
                >
                  <HStack>
                    <Text color='#909090' fontWeight={'semibold'}>
                      #{active.order_id}
                    </Text>
                    <Spacer />
                    <Text>
                      Status:
                      <Tag
                        ml={2}
                        bg={backgroundColor}
                        textColor={textColor}
                        rounded='full'
                      >
                        {active.order_status}
                      </Tag>
                      {status[2] && (
                        <Tag
                          rounded='full'
                          bg={status[1]}
                          textTransform='uppercase'
                          textColor={status[0]}
                        >
                          {status[2]}
                        </Tag>
                      )}
                    </Text>
                  </HStack>
                  <Text
                    mt={4}
                    cursor={'pointer'}
                    color='#FB8500'
                    onClick={drawer.onOpen}
                  >
                    VIEW ORDER TIMELINE
                  </Text>
                  {remarkData && (
                    <Box mt={4}>
                      <Text color='#909090'>REMARKS</Text>
                      <Text>{remarkData.remark}</Text>
                      <Text fontSize={'sm'} color='#909090'>
                        {format(new Date(remarkData.remark_time), 'PPP HH:mm')}
                      </Text>
                    </Box>
                  )}
                </Box>
                <Stack
                  p={4}
                  spacing={4}
                  roundedBottom='lg'
                  borderWidth={'1px'}
                  borderColor={'#DEDEDE'}
                >
                  <Box>
                    <Text color='#909090'>Order Date</Text>
                    <Text>
                      {format(new Date(active.order_time), 'PPP HH:mm')}
                    </Text>
                  </Box>

                  <Box>
                    <Text color='#909090'>Coal Type</Text>
                    <Text>{active.coal_name}</Text>
                  </Box>

                  <Box>
                    <Text color='#909090'>Port Name</Text>
                    <Text>{active.port_name}</Text>
                  </Box>

                  <Box>
                    <Text color='#909090'>Vessel Name</Text>
                    <Text>{active.vessel_name}</Text>
                  </Box>
                  {conditionKeys.map((key) => {
                    if (condition_details[key] !== null) {
                      const value = condition_details[key]
                      if (value.length > 1)
                        return (
                          <Box>
                            <Text textTransform={'capitalize'} color='#909090'>
                              {key.replace(/\_/g, ' ')}
                            </Text>
                            <Text>{value}</Text>
                          </Box>
                        )
                    }
                  })}
                  <Box>
                    <Text color='#909090'>Quantity</Text>
                    <Text>
                      {active.quantity} {active.unit}
                    </Text>
                  </Box>
                  <Box>
                    <Text color='#909090'>Validity</Text>
                    <Text>{format(new Date(active.expiry), 'PPP HH:mm')}</Text>
                  </Box>
                  <Box>
                    <Text color='#909090'>Bid Price</Text>
                    <Text fontSize={'xl'} fontWeight='semibold'>
                      {Number(active.bid_price).toLocaleString()} - PMT
                    </Text>
                    <Text mb={4} color='#909090' fontSize={'sm'}>
                      Rs. {Number(active.bid_price - 400).toLocaleString()} PMT
                      + 5% + Rs. 400 PMT
                    </Text>
                  </Box>
                </Stack>
              </Stack>
            )
          )}
        </Stack>
      </Flex>

      <OrderTimeline remarks={active.remarks} {...drawer} />
    </Box>
  )
}

export default MyOrders
