import {
  Box,
  Center,
  HStack,
  Spacer,
  Text,
  useBreakpointValue,
} from '@chakra-ui/react'
import { Link } from 'react-router-dom'
import { TrailDataStore } from '../store/TrialData'

function TrailBanner() {
  const trailData = TrailDataStore.useState((s) => s.data)

  return (
    <Box bg={'#FEF3E5'} px={{ base: 7, lg: 24 }} py={4}>
      <HStack spacing={{ base: 0, lg: 12 }} d={{ base: 'block', lg: 'flex' }}>
        <Text fontSize={{ base: 'sm', lg: 'lg' }}>
          You are using basic membership, to buy coal upgrade to premium
          membership
        </Text>
        <Spacer />
        <HStack spacing={4}>
          <Link to={'/upgrade'}>
            <Text
              p={2}
              rounded={'full'}
              bg={'#FB8502'}
              fontSize={'sm'}
              cursor={'pointer'}
              textColor={'white'}
              fontWeight={'bold'}
              mt={{ base: 4, lg: 0 }}
            >
              {useBreakpointValue({
                base: 'Upgrade',
                lg: 'Upgrade Now',
              })}
            </Text>
          </Link>
          <Center>
            <Box
              h={'25px'}
              mt={{ base: 4, lg: 0 }}
              bg={'black'}
              w={'0.4px'}
              d={trailData?.trial ? 'flex' : 'none'}
            ></Box>
          </Center>
          <Box>
            <Link to={'/buy-coal'}>
              <Text
                rounded={'full'}
                textColor={'#FB8500'}
                textTransform={'uppercase'}
                bg={'#FAE5CE'}
                p={2}
                mt={{ base: 4, lg: 0 }}
                fontSize={'sm'}
                cursor={'pointer'}
                d={trailData?.trial ? 'flex' : 'none'}
              >
                {trailData?.trial_remaining > 0
                  ? 'Trail BIDS LEFT: ' + trailData?.trial_remaining
                  : 'ACTIVATE NOW'}
              </Text>
            </Link>
          </Box>
        </HStack>
      </HStack>
    </Box>

    // {trailData?.trial_remaining}
  )
}

export default TrailBanner
