import { Heading, Text, Box, HStack } from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import { FiArrowLeft } from 'react-icons/fi'
import useFetch from 'use-http'

function Bank() {
  const bankApi = useFetch('/api/listing/banks')
  const subscriptionFeesApi = useFetch('/api/listing/subscription_fees')
  const [banks, setBanks] = useState<any[]>([])
  const [subscriptionFees, setSubscriptionFees] = useState<any>('')

  useEffect(() => {
    bankApi.post().then((res) => {
      setBanks(res.data)
    })

    subscriptionFeesApi.post().then((res) => {
      setSubscriptionFees(res.data)
      // console.log(res.data)
    })
  }, [])
  return (
    <Box m={'0 auto'} maxW={'4xl'} mt={8} px={{ base: 8, lg: 2 }}>
      <HStack spacing={6}>
        <Box
          fontSize={{ base: '2xl', lg: '4xl' }}
          onClick={() => {
            window.history.back()
          }}
          cursor={'pointer'}
        >
          <FiArrowLeft />
        </Box>
        <Heading size={'lg'}>Bank Details</Heading>
      </HStack>
      <Box>
        <Heading mt={8} textTransform={'uppercase'} size={'md'}>
          Pay security amount offline using rtgs/neft
        </Heading>
      </Box>
      <Text fontSize={'2xl'} mt={6} textColor={'gray.600'}>
        SECURITY AMOUNT
      </Text>
      <Text textColor={'gray.800'} fontSize={'2xl'}>
         {subscriptionFees}
      </Text>

      {banks.map((bnk, idx) => {
        return (
          <Box key={idx} pb={10} mt={4}>
            <Heading size={'xl'}>{bnk.bank_name}</Heading>

            {bnk.bank_details.split('\n').map((line, idx) => {
              return (
                <Text mt={2} key={idx}>
                  {line}
                </Text>
              )
            })}
          </Box>
        )
      })}
    </Box>
  )
}

export default Bank
