import React, { useEffect, useState } from 'react'
import {
  Box,
  Heading,
  HStack,
  Radio,
  RadioGroup,
  Stack,
  FormControl,
  FormLabel,
  Input,
  Button,
  Text,
  Flex,
} from '@chakra-ui/react'
import { useForm } from 'react-hook-form'
import { FiPlus, FiX } from 'react-icons/fi'
import useFetch from 'use-http'

const Register: React.FC = () => {
  const [userType, setUserType] = useState('')
  const [companies, setCompanies] = useState<any>([
    {
      name: '',
      gstno: '',
    },
  ])
  const registerApi = useFetch('/login/register')
  const { register, handleSubmit } = useForm()
  const favCoalApi = useFetch('/api/listing/fav_coal')
  const [favCoals, setFavCoals] = useState([])
  const [selectedfavCoal, setSelectedfavCoal] = useState<String[]>([])

  useEffect(() => {
    favCoalApi.post().then((res) => {
      setFavCoals(res.data)
    })
  }, [])

  const onSubmit = (data: any) => {
    registerApi
      .post({
        user_type: userType,
        companies,
        name: data.f_name + ' ' + data.l_name,
      })
      .then((data) => {
        if (data.message === 'Success') {
          window.location.href = '/'
        }
      })
  }

  return (
    <Box py={10} maxW='xl' m='0 auto'>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Heading size={'lg'} fontWeight={'normal'} textAlign={'center'}>
          Complete your registration process
        </Heading>
        <Stack mt={8} spacing={6}>
          <FormControl as='fieldset'>
            <FormLabel as='legend' fontWeight={'bold'}>
              Select type of user
            </FormLabel>
            <RadioGroup
              onChange={(e) => {
                setUserType(e)
              }}
              pb={6}
            >
              <HStack spacing='24px'>
                <Radio value='end_user'>End user</Radio>
                <Radio value='trader'>Trader</Radio>
                <Radio value='both'>Both</Radio>
              </HStack>
            </RadioGroup>
          </FormControl>

          <FormControl>
            <Input {...register('f_name')} placeholder='First Name' />
          </FormControl>
          <FormControl>
            <Input {...register('l_name')} placeholder='Last Name' />
          </FormControl>

          <FormControl>
            <Stack spacing={2} mt={2}>
              {companies.map((company: any, index: number) => (
                <HStack key={index}>
                  <Input
                    placeholder='Company Name'
                    value={company.name}
                    onChange={(e) => {
                      const _companies = [...companies]
                      _companies[index] = {
                        ...companies[index],
                        name: e.target.value,
                      }
                      setCompanies(_companies)
                    }}
                  />
                  <Input
                    placeholder='GST No.'
                    value={company.gstno}
                    onChange={(e) => {
                      const _companies = [...companies]
                      _companies[index] = {
                        ...companies[index],
                        gstno: e.target.value,
                      }
                      setCompanies(_companies)
                    }}
                  />
                  <FiX
                    size={24}
                    onClick={() => {
                      const f = companies.filter(
                        (c: any, i: number) => i !== index
                      )
                      setCompanies(f)
                    }}
                  />
                </HStack>
              ))}
            </Stack>

            <Button
              mt={8}
              leftIcon={<FiPlus />}
              onClick={() => {
                setCompanies([...companies, { name: '', gst: '' }])
              }}
            >
              Add more
            </Button>

            <Text mt={10}>Preferred Coal Types</Text>

            <Flex flexWrap={'wrap'}>
              {favCoals.map((favCoal: any, index: number) => (
                <Box
                  p={2}
                  mt={2}
                  mr={2}
                  key={index}
                  cursor={'pointer'}
                  borderWidth={
                    selectedfavCoal.includes(favCoal._id) ? '2px' : '1px'
                  }
                  rounded='lg'
                  borderColor={
                    selectedfavCoal.includes(favCoal._id)
                      ? 'red.400'
                      : 'gray.300'
                  }
                  onClick={() => {
                    if (selectedfavCoal.includes(favCoal._id)) {
                      const _selectedfavCoal = selectedfavCoal.filter(
                        (c: any) => c !== favCoal._id
                      )

                      setSelectedfavCoal(_selectedfavCoal)
                    } else {
                      setSelectedfavCoal([...selectedfavCoal, favCoal._id])
                    }
                  }}
                >
                  {favCoal.coal}
                </Box>
              ))}
            </Flex>
          </FormControl>
          <FormControl>
            <Button type='submit' colorScheme={'orange'}>
              Create Account
            </Button>
          </FormControl>
        </Stack>
      </form>
    </Box>
  )
}

export default Register
