import React, { forwardRef, useEffect, useMemo, useState } from 'react'
import {
  Box,
  FormControl,
  FormLabel,
  Select,
  SimpleGrid,
  Spinner,
  Center,
  Heading,
  Text,
  Divider,
  Stack,
  Input,
  HStack,
  RadioGroup,
  Badge,
  Radio,
  Button,
  Spacer,
  Drawer,
  DrawerOverlay,
  DrawerContent,
  DrawerHeader,
  DrawerBody,
  GridItem,
} from '@chakra-ui/react'
import useFetch from 'use-http'
import { CoalType, Port, Vessel } from '../../types'
import { BuyCoalStore } from '../../store/BuyCoal'
import ConditionCard from './condition'
import ConditionInfo from './condition-info'
import { FiArrowLeft, FiDownload, FiInfo } from 'react-icons/fi'
import DatePicker from 'react-datepicker'

import 'react-datepicker/dist/react-datepicker.css'
import { differenceInMinutes } from 'date-fns'
import { UserStore } from '../../store/user'

interface SelectedState {
  coalId?: string
  portId?: string
  vesselId?: string
  noOfTruck?: number
  conditionId?: number
  condition?: any
  company?: string
}

type Props = {
  continue: () => void
}

const ExampleCustomInput = forwardRef<any>(({ value, onClick }: any, ref) => (
  <Button variant={'link'} onClick={onClick} ref={ref}>
    Edit time
  </Button>
))

const BuyCoalStep1: React.FC<Props> = (props) => {
  const { user } = UserStore.useState()
  const [loading, setLoading] = useState(false)
  const coalTypeApi = useFetch('/api/listing/buyCoalList')
  const filteredPortApi = useFetch('/api/listing/getFilteredPort')
  const filteredVesselApi = useFetch('/api/listing/getFilteredVessel')
  const conditionApi = useFetch('/api/listing/getFilteredCondition')
  const [selectedCondition, setSelectedCondition] = useState(0)
  const [activeCondition, setActiveCondition] = useState<any>({})
  const [vesselStatusPorts, setvesselStatusPorts] = useState<any[]>([])
  const [showSummary, setShowSummary] = useState(false)
  const [summary, setSummary] = useState<any>({
    'Order Date': '10 mar 2021',
  })

  const [company, setCompany] = useState(user.companies[0]._id)
  const [noBidCoal, setNoBidCoal] = useState<any>('')
  const [noBids, setNoBids] = useState(false)
  const [noBidsMessage, setNoBidsMessage] = useState('')

  const [selected, setSelected] = useState<SelectedState>({})

  const [coalType, setCoalType] = useState<CoalType[]>([])

  const [port, setPort] = useState<Port[]>([])
  const [vessel, setVessel] = useState<Vessel[]>([])
  const [conditions, setConditions] = useState<any[]>([])
  const [bidPrice, setBidPrice] = useState(0)

  const [validity, setValidity] = useState('6 HRS 30 MIN')
  const [validityTime, setValidityTime] = useState(
    new Date(new Date().getTime() + 390 * 60000)
  )
  const [minValidity, setMinValidity] = useState(
    new Date(new Date().getTime() + 390 * 60000)
  )

  const [maxValidity, setMaxValidity] = useState(
    new Date(new Date().getTime() + 1440 * 60000)
  )
  const [qType, setQType] = useState('MT')
  const [quantity, setQuantity] = useState('')

  const vesselLink = useMemo(() => {
    const v = vessel.find((v: any) => v.id === selected.vesselId)
    return v?.vessel.vessel_link
  }, [selected.vesselId])

  useEffect(() => {
    setSummary({
      ...summary,
      Quantity: `${quantity} ${qType}`,
    })
  }, [quantity])

  const summaryKeys = useMemo(() => {
    return Object.keys(summary)
  }, [summary])

  useEffect(() => {
    BuyCoalStore.update((s) => {
      s.data.bid_price = bidPrice
      s.data.add_buy_coal_port_vessel_condition_id = String(activeCondition._id)
      s.data.company = company

      s.data.unit = qType
      s.data.quantity = quantity
      s.data.validity_in_minutes = differenceInMinutes(validityTime, new Date())
    })
  }, [selected, bidPrice, qType, quantity])

  const onCoalTypeChange = (coalId: string) => {
    setLoading(true)
    setConditions([])
    setSelected({
      coalId,
    })
    setPort([])
    setVessel([])
    setActiveCondition({})
    setNoBids(false)
    setNoBidsMessage('')

    const coal = coalType.find((c) => c.coal._id === coalId)
    setSummary({
      ...summary,
      'Coal Type': coal?.coal.coal_name,
    })
    filteredPortApi
      .post({ coal_id: coalId })
      .then((data) => {
        const vesselStatus = data.data.find((i: any) => i.vessel_status)

        setLoading(false)

        if (!data.bids_open) {
          setNoBids(true)
          setNoBidsMessage(data.message)
          return
        }

        if (!data.bids_allowed) {
          setNoBidsMessage('BIds not aallllwed')
          setNoBids(true)
          setNoBidCoal(coal?.coal.coal_name)
          return
        }
        setNoBidCoal('')

        if (vesselStatus) {
          setvesselStatusPorts([...vesselStatusPorts, vesselStatus._id])
        }

        setPort(data.data)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  const onPortChange = (portId: string) => {
    setLoading(true)
    setSelected({
      ...selected,
      portId,
    })
    setVessel([])
    setConditions([])
    setActiveCondition({})

    filteredVesselApi
      .post({ add_buy_coal_port_id: portId })
      .then((data) => {
        setLoading(false)
        if (!vesselStatusPorts.includes(portId)) {
          onVesselChange(data.data[0]._id)
          return
        }
        setVessel(data.data)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  const onVesselChange = (vesselId: string) => {
    setLoading(true)
    setConditions([])
    setActiveCondition({})

    conditionApi
      .post({ add_buy_coal_port_vessel_id: vesselId })
      .then((data) => {
        setLoading(false)
        setConditions(data.data)
        if (data.data.length === 0) {
          return
        }

        const selectedVessel = vessel.find((v, idx) => {
          return v.id === vesselId
        })

        setSummary({
          ...summary,
          Vessel: selectedVessel?.vessel.vessel_name,
        })

        const firstCondition = data.data[0]
        setActiveCondition(firstCondition)

        setSelected({
          ...selected,
          vesselId,
        })

        if (firstCondition.unit === 'Both') {
          setQType('MT')
        } else setQType(firstCondition.unit)

        if (firstCondition.min_validity_in_minutes) {
          const minDate = new Date(
            new Date().getTime() +
              firstCondition.min_validity_in_minutes * 60000
          )

          setValidityTime(minDate)
          setMinValidity(minDate)
        }

        setValidity(
          `${Math.floor(firstCondition.min_validity_in_minutes / 60)} HRS ${
            firstCondition.min_validity_in_minutes % 60
          } MIN`
        )

        if (firstCondition.max_validity_in_minutes) {
          setMaxValidity(
            new Date(
              new Date().getTime() +
                firstCondition.max_validity_in_minutes +
                30440 * 60000
            )
          )
        }

        setQuantity('')

        setSelected({
          ...selected,
          vesselId,
        })
      })
  }

  useEffect(() => {
    coalTypeApi.post().then((data) => {
      setCoalType(data?.data)
    })
  }, [])

  const minQ = useMemo(
    () =>
      qType === 'Truck'
        ? activeCondition?.min_truck_quantity || 1
        : activeCondition?.min_mt_quantity || 36,
    [activeCondition, qType]
  )
  const maxQ = useMemo(
    () =>
      qType === 'Truck'
        ? activeCondition?.max_truck_quantity || null
        : activeCondition?.max_mt_quantity || null,
    [activeCondition, qType]
  )

  useEffect(() => {
    setSummary({
      ...summary,
      'Lifting Period':
        activeCondition?.condition?.condition_details.lifting_period,
      Quality: activeCondition?.condition?.condition_details.quality,
      Payment: activeCondition?.condition?.condition_details.payment,
    })
  }, [activeCondition])

  const qTypePicker = useMemo(() => {
    return activeCondition && activeCondition?.unit === 'Both'
  }, [activeCondition])

  const qError = useMemo(() => {
    if (qType === 'Truck') {
      if (quantity > maxQ) {
        return `Quantity should be less than ${maxQ}`
      }
      if (quantity < minQ) {
        return `Quantity should be greater than ${minQ}`
      }
    } else {
      if (quantity > maxQ) {
        return `Quantity should be less than ${maxQ}`
      }
      if (quantity < minQ) {
        return `Quantity should be greater than ${minQ}`
      }
    }
    return null
  }, [minQ, maxQ, quantity])

  const OpenSummary = () => {
    setShowSummary(true)
  }

  return (
    <Box>
      <Center mb={12}>{loading && <Spinner />}</Center>
      <SimpleGrid mb={12} spacing={6} columns={{ base: 1, lg: 2 }}>
        <FormControl>
          <FormLabel>Company</FormLabel>
          <Select
            onChange={(e) => {
              setCompany(e.target.value)
            }}
          >
            {user?.companies?.map((comp, idx) => (
              <option key={idx} value={comp._id}>
                {comp.company_name}
              </option>
            ))}
          </Select>
        </FormControl>

        <FormControl>
          <FormLabel>Coal Type</FormLabel>
          <Select
            placeholder='Select Coal Type'
            onChange={(e) => onCoalTypeChange(e.target.value)}
          >
            {coalType?.map((item, idx) => (
              <option key={idx} value={item.coal._id}>
                {item.coal.coal_name}
              </option>
            ))}
          </Select>
        </FormControl>

        {!noBids ? (
          <FormControl>
            <FormLabel>Port</FormLabel>
            <Select
              isDisabled={!selected.coalId}
              placeholder='Select Port'
              onChange={(e) => onPortChange(e.target.value)}
            >
              {port.map((item, idx) => (
                <option key={idx} value={item.id}>
                  {item.port.port_name}
                </option>
              ))}
            </Select>
          </FormControl>
        ) : (
          <Text>{noBidsMessage || noBidCoal}</Text>
        )}
        {vessel.length > 0 && (
          <>
            <FormControl>
              <FormLabel>Vessel</FormLabel>
              <Select
                isDisabled={!selected.portId}
                placeholder='Select Vessel'
                onChange={(e) => onVesselChange(e.target.value)}
              >
                {vessel.map((item, idx) => (
                  <option key={idx} value={item.id}>
                    {item.vessel.vessel_name}
                  </option>
                ))}
              </Select>
              {vesselLink && (
                <HStack
                  mt={3}
                  alignItems='center'
                  onPress={() => {
                    window.open(vesselLink)
                  }}
                  cursor='pointer'
                >
                  <FiDownload color='#FB8500' />
                  <Text ml={2} color='#FB8500' fontWeight={'semibold'}>
                    Click here to download report
                  </Text>
                </HStack>
              )}
            </FormControl>
          </>
        )}
      </SimpleGrid>

      <Stack spacing={4} d={conditions.length > 1 ? 'flex' : 'none'}>
        <Box>
          <Heading size='md'>Select any condition</Heading>
          <Text mt={2}>
            Based on selected condition, indicative price will be displayed
          </Text>
        </Box>
        <RadioGroup>
          <HStack
            overflow={'auto'}
            d={conditions.length === 1 ? 'none' : 'flex'}
          >
            {conditions?.map((condition, idx) => (
              <ConditionCard
                key={idx}
                title={`Condition ${idx + 1}`}
                isSelected={selectedCondition === idx}
                onSelect={() => {
                  setSelectedCondition(idx)
                  setSelected({
                    ...selected,
                    condition: condition,
                  })
                  setQType(condition.unit)
                  setActiveCondition(condition)
                }}
                condition={condition}
              />
            ))}
          </HStack>
        </RadioGroup>

        <Divider my={6} />
      </Stack>

      <Box d={activeCondition?.price ? 'block' : 'none'} rounded='lg'>
        <Text color='gray.500' fontSize='lg'>
          Indicative Price
        </Text>
        <Heading fontSize={'xl'} fontWeight={'bold'} mt={1}>
           {Number(activeCondition?.price).toLocaleString()}/- PMT
        </Heading>
        <Text my={2}>
          Rs. {Number(activeCondition?.price - 400).toLocaleString()} PMT + 5% +
          Rs. 400 PMT
        </Text>

        <ConditionInfo
          data={activeCondition?.condition?.condition_details || {}}
        />

        <Box mt={4}>
          <Heading size='md' mb={3} fontWeight={'semibold'}>
            Quantity
          </Heading>
          {qTypePicker && (
            <RadioGroup
              value={qType}
              onChange={(e) => setQType(e)}
              name='qType'
              colorScheme={'red'}
            >
              <HStack space={4}>
                <Radio size='sm' value='Truck'>
                  BY TRUCK
                </Radio>
                <Radio size='sm' value='MT'>
                  BY METRIC TON
                </Radio>
              </HStack>
            </RadioGroup>
          )}

          {!qTypePicker ? (
            activeCondition.unit === 'Truck' ? (
              <Text>Enter Quantity in Truck</Text>
            ) : (
              <Text>Enter Quantity in MT</Text>
            )
          ) : null}

          <Input
            mt={4}
            keyboardType='numeric'
            onChange={(e) => setQuantity(e.target.value)}
            placeholder={
              qType === 'Truck'
                ? 'Enter number of trucks'
                : 'Enter quantity in MT'
            }
          />
          <HStack mt={4} alignItems={'center'}>
            <FiInfo />
            {minQ && (
              <Badge p={1} ml={2} rounded='full'>
                {`Min ${qType === 'Truck' ? 'trucks' : 'quantity'}: ${minQ} ${
                  qType === 'MT' ? 'MT' : ''
                }`}
              </Badge>
            )}

            {maxQ && (
              <Badge p={1} rounded='full'>{`Max ${
                qType === 'Truck' ? 'trucks' : 'quantity'
              }: ${maxQ} ${qType === 'MT' ? 'MT' : ''}`}</Badge>
            )}
          </HStack>
          <Text mt={4} color='red.400'>
            {qError}
          </Text>
        </Box>

        <Box mt={4}>
          <Heading size='md'>Enter your bid price</Heading>
          <Box mt={4}>
            <HStack p={2} bg='red.100' roundedTop='xl'>
              <Text></Text>
              <Text ml={2}>
                Selected Indicative Price is {' '}
                {Number(activeCondition?.price).toLocaleString()} PMT
              </Text>
            </HStack>
            <Box>
              <Input
                py={4}
                placeholder='Enter your bid price'
                keyboardType='numeric'
                onChange={(e) => {
                  setBidPrice(Number(e.target.value || 0))
                }}
                InputLeftElement={
                  <Text fontSize={'3xl'} px={2}>
                    
                  </Text>
                }
                InputRightElement={
                  <Text color='red.500' fontWeight={'bold'} mr={3}>
                    BID
                  </Text>
                }
                size='lg'
              />
              {bidPrice > 400 && (
                <Text mt={2}>
                  Rs. {Number(bidPrice - 400).toLocaleString()}
                  PMT + 5% + Rs. 400 PMT
                </Text>
              )}
            </Box>
          </Box>
        </Box>

        <Box mt={4}>
          <Heading size='md' mb={4}>
            Validity
          </Heading>
          <HStack w='full'>
            <HStack>
              <Spacer />
              <Text>Time Left:</Text>
              <Text>{validity}</Text>
            </HStack>
            <Spacer />
            <Box>
              <DatePicker
                showTimeSelect
                minDate={minValidity}
                maxDate={maxValidity}
                value={validity}
                onChange={(date) => {
                  if (!date) return
                  setValidityTime(date)

                  const time = new Date(date)

                  const diffMins = differenceInMinutes(time, new Date())

                  setValidity(
                    `${Math.floor(diffMins / 60)} HRS ${diffMins % 60} MIN`
                  )
                }}
                customInput={<ExampleCustomInput />}
              />
            </Box>
          </HStack>
        </Box>
      </Box>

      <Button
        mt={4}
        rounded='full'
        colorScheme={'orange'}
        onClick={OpenSummary}
      >
        Bid Offer
      </Button>
      <Box>
        <Drawer
          isOpen={showSummary}
          placement='right'
          onClose={() => {
            setShowSummary(false)
          }}
          size={'lg'}
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerHeader>
              <HStack spacing={8}>
                <Box
                  fontSize={'3xl'}
                  onClick={() => {
                    setShowSummary(false)
                  }}
                  cursor={'pointer'}
                >
                  <FiArrowLeft />
                </Box>
                <Text fontWeight={'bold'} fontSize={'2xl'}>
                  Your order summary
                </Text>
              </HStack>
            </DrawerHeader>
            <DrawerBody>
              <SimpleGrid columns={6} spacing={8}>
                {summaryKeys.map((key) => {
                  const val = summary[key]
                  if (!val) return null

                  return (
                    <>
                      <GridItem colSpan={2}>
                        <Text textColor={'gray.500'}>{key}</Text>
                      </GridItem>
                      <GridItem colSpan={4}>
                        <Text fontWeight={'bold'}>{val}</Text>
                      </GridItem>
                    </>
                  )
                })}
              </SimpleGrid>
              <Box mt={4}>
                <Divider />
              </Box>
              <Box mt={4}>
                <HStack>
                  <Text>Price BID</Text>
                  <Spacer />
                  <Text fontSize={'3xl'} fontWeight={'bold'}>
                     {bidPrice.toLocaleString()} /-PMT
                  </Text>
                </HStack>
              </Box>
              <Box mt={6}>
                <Button
                  fontWeight={'bold'}
                  colorScheme={'orange'}
                  rounded={'full'}
                  w={'full'}
                  textColor={'white'}
                  onClick={props.continue}
                >
                  PLACE YOUR BID
                </Button>
              </Box>
            </DrawerBody>
          </DrawerContent>
        </Drawer>
      </Box>
    </Box>
  )
}

export default BuyCoalStep1
